.el-login{
    padding: 50px 0;
    text-align: center;
    form{
        padding: 20px 30px;
        border: 1px solid #dddddd;
        border-radius: 10px;
        width: 85%;
        margin: 0 auto;
        box-shadow: 6px 7px 19px 0px rgba(0,0,0,0.05);
        display: inline-flex;
        flex-wrap: wrap;
        justify-content: center; 


        @include tablet{
            max-width: 600px;
            padding: 50px 70px;
        }
        legend{
            font-size: 35px;
            font-family: Montserrat Bold;
            margin-bottom: 30px;
            width: 100%;
            text-align: left;
        }
        input{
            border: 1px solid #dbdbdb;
            border-radius: 10px;
            width: 100%;
            margin-bottom: 30px;
            margin-right: 0px;
        }
        p{
            margin-bottom: 30px;
            text-align: left;
            font-size: 14px;
            width: 100%;
            a{
                color: $color_background_botao;
            }
        }
        input[type="submit"]{
            text-transform: uppercase;
            font-size: 25px;
            padding: 15px 35px;
            height: auto;
            line-height: 1;
            width: auto;
            border-radius: 10px;
            margin-bottom: 0px;
        }
    }
}